<template>
  <div class="box">
    <div class="head"></div>
    <ul class="list">
      <li @click="invita(i)" v-for="i in friendList">
        <div class="left">
          <img :src="i.img_url"/>
          <a>{{i.user_nick}}</a>
        </div>
        <img class="right" v-if="i.num >= 2" src="../../assets/images/activity/51wawa37.png"/>
        <img class="right" v-if="i.num < 2" src="../../assets/images/activity/51wawa38.png"/>
      </li>
    </ul>
    <div class="back">
      <p @click="back">返回我的娃娃机</p>
    </div>
  </div>
</template>

<script>
  var urlhost = process.env.API_AJAX;
  import defalut from '@/assets/images/home/defalut.png'

  export default {
    name:"",
    data() {
      return {
        defalut:defalut,
        friendList:[]
      }
    },
    components: {
    },
    mounted:function () {
      var _this= this;
      $.ajax({
        type: 'POST',
        url: urlhost+"active/getlist",
        data$Type: "jsonp",
        data: {

        },
        success: function (response) {
          var data = JSON.parse(response).message;
          var friends = [];
          data.forEach(function (i) {
            if(i.img_url == "" || i.img_url == null){
              i.img_url = _this.defalut;
            }
            friends.push(i);
          })
          _this.friendList = friends;
        },
      });
    },
    methods:{
      invita:function (i) {
        var _this = this;
        _this.$router.push({
          path:"/activity/otherHome",
          query:{
            id:i.user_id
          }
        })
      },
      back:function () {
        var _this = this;
        _this.$router.push({
          path:"/activity/activityHome"
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
  @function n($n) {
    @return $n  / 64 + rem;
  };
.box{
  position: fixed;
  top:0;
  width: 100%;
  height: 100%;
  .head{
    width: 100%;
    height: n(141);
    background: url("https://fkzzz.oss-cn-qingdao.aliyuncs.com/img/51wawa36.png") no-repeat;
    background-size: 100% 100%;
  }
  .list{
    overflow-y: scroll;
    height: n(770);
    li{
      width: 100%;
      height: n(100);
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px solid #ececec;
      .left{
        margin-left: n(50);
        img{
          width: n(60);
          height: n(60);
        }
        a{
          font-size: n(20);
          margin-left: n(20);
        }
      }
      .right{
        margin-right: n(50);
        width: n(50);
        height: n(50);
      }

    }

  }
  .back{
    position: absolute;
    bottom:n(50);
    width: 100%;
    height: n(60);
    p{
      font-size: n(30);
      color: #ffffff;
      margin-left:25%;
      -webkit-border-radius:20px;
      -moz-border-radius:20px;
      border-radius:20px;
      width: 50%;
      height: n(60);
      display: inline-flex;
      justify-content: center;
      align-items: center;
      background: #ff7f76;
      box-shadow:  0px 5px 5px 0px #d54745;
    }
  }
}
</style>



